<template>
  <page-view ref="pageView">
    <template slot="header-bar">
      <div v-if="type == 'info' && id != -1">
        <a-button icon="edit" type="primary" @click="changeType('edit')"
          >修改</a-button
        >
        <a-button type="danger" @click="deleteConfig">删除</a-button>
      </div>
      <div v-if="type == 'edit'">
        <a-button icon="edit" type="primary" @click="editSave">保存</a-button>
        <a-button type="danger" @click="changeType('info')">取消</a-button>
      </div>
      <div v-if="type == 'add'">
        <a-button icon="edit" type="primary" @click="save">保存</a-button>
      </div>
      <div v-if="id == -1 && type == 'info'">
        <a-button icon="edit" type="primary" @click="copy">复制</a-button>
      </div>
    </template>
    <div class="body" slot="body">
      <a-row class="config-item">
        <a-row class="item-label"> 基础配置 </a-row>
        <a-row class="item-view" :gutter="[16, 16]">
          <a-form :form="form" class="pl-form-line">
            <a-col :span="24">
              <a-form-item label="配置名称">
                <a-input
                  :required="true"
                  v-model="title"
                  :disabled="type == 'info'"
                />
              </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item label="基础包名">
                <a-input
                  v-model="formData.packageName"
                  :disabled="type == 'info'"
                />
              </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item label="作者">
                <a-input v-model="formData.author" :disabled="type == 'info'" />
              </a-form-item>
            </a-col>
            <a-col :span="8">
              <a-form-item label="表前缀">
                <a-input
                  v-model="formData.tablePrefix"
                  :disabled="type == 'info'"
                />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="是否装配">
                <a-switch v-model="formData.wired" :disabled="type == 'info'" />
                <a-popover placement="topLeft">
                  <template slot="content">
                    <div class="qa-content">
                      <div>
                        1.<span class="code">Servicer</span
                        >层将装配所对应的<span class="code">Mapper</span>。
                      </div>
                      <div>
                        2.<span class="code">Controller</span
                        >层将装配所对应的<span class="code">Service</span>。
                      </div>
                    </div>
                  </template>
                  <a-icon
                    style="margin-left: 10px; cursor: pointer"
                    type="alert"
                  />
                </a-popover>
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="swagger">
                <a-switch
                  v-model="formData.swagger"
                  :disabled="type == 'info'"
                />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="装配方式">
                <a-radio-group
                  name="radioGroup"
                  v-model="formData.wiredType"
                  :disabled="type == 'info' || !formData.wired"
                >
                  <a-radio value="resource"> @Resource </a-radio>
                  <a-radio value="autoWired"> @AutoWired </a-radio>
                  <a-radio value="constructor"> 构造方法 </a-radio>
                </a-radio-group>
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="代码风格">
                <a-radio-group
                  name="radioGroup"
                  :disabled="type == 'info'"
                  v-model="formData.style"
                >
                  <a-radio value="pl-web"> PL-WEB </a-radio>
                  <a-radio value="mybatis-plus"> MyBatis-Plus </a-radio>
                  <a-radio value="mybatis"> MyBatis </a-radio>
                </a-radio-group>
              </a-form-item>
            </a-col>
          </a-form>
        </a-row>
      </a-row>

      <a-row class="config-item">
        <a-row class="item-label"> 扩展配置 </a-row>
        <a-row class="item-view">
          <a-tabs
            default-active-key="typeMapperConfig"
            @tabClick="bindOnTabChange"
          >
            <a-tab-pane key="typeMapperConfig" tab="类型映射" :forceRender="true">
              <type-mapper-config
                ref="typeMapperConfig"
                :disabled="type == 'info'"
                :config="formData.typeMapperConfig"
              ></type-mapper-config>
            </a-tab-pane>
            <a-tab-pane key="columnConfig" tab="字段配置" force-render :forceRender="true">
              <column-config
                ref="columnConfig"
                :disabled="type == 'info'"
                :config="formData.columnConfig"
              ></column-config>
            </a-tab-pane>
            <a-tab-pane key="entityConfig" tab="Entity配置" :forceRender="true">
              <entity-config
                ref="entityConfig"
                :disabled="type == 'info'"
                :config="formData.entityConfig"
              ></entity-config>
            </a-tab-pane>
            <a-tab-pane key="mapperConfig" tab="Mapper配置" :forceRender="true">
              <mapper-config
                ref="mapperConfig"
                :disabled="type == 'info'"
                :config="formData.mapperConfig"
              ></mapper-config>
            </a-tab-pane>
            <a-tab-pane key="controllerConfig" tab="Controller配置" :forceRender="true">
              <controller-config
                ref="controllerConfig"
                :disabled="type == 'info'"
                :config="formData.controllerConfig"
              ></controller-config>
            </a-tab-pane>
          </a-tabs>
        </a-row>
      </a-row>
    </div>
  </page-view>
</template>

<script>
import {
  configDetail,
  addConfig,
  updateConfig,
  removeConfig,
} from "@/apis/config";
import { defaultConfig } from "@/common/default-generator-config";
import TypeMapperConfig from "@/page/config/modules/type-mapper-config";
import ColumnConfig from "@/page/config/modules/column-config";
import EntityConfig from "@/page/config/modules/entity-config";
import MapperConfig from "@/page/config/modules/mapper-config";
import ControllerConfig from "@/page/config/modules/controller-config";

export default {
  name: "info",
  components: {
    ControllerConfig,
    MapperConfig,
    EntityConfig,
    ColumnConfig,
    TypeMapperConfig,
  },
  data() {
    return {
      title: "",
      formData: defaultConfig,
      form: this.$form.createForm(this, { name: "horizontal_login" }),
      id: null,
      type: "info",
    };
  },
  created() {
    const type = this.$route.params.type || "add";
    this.type = type;
    const id = this.$route.params.id;
    if (id) {
      this.id = id;
      this.getConfigDetail();
    }
    console.log(type);
  },
  mounted() {
    //this.renderConfig();
  },
  methods: {
    getConfigDetail() {
      configDetail(this.id).then((data) => {
        const config = data.config;
        console.log(config);
        this.title = data.name;
        this.formData = config;
        this.$refs.pageView.setTitle(data.name);
        this.renderConfig();
      });
    },
    renderConfig() {
      // if (this.formData.typeMapperConfig && this.$refs.typeMapperConfig) {
      //   this.$refs.typeMapperConfig.render(this.formData.typeMapperConfig);
      // }
    },
    bindOnTabChange() {
      //this.renderConfig();
    },
    changeType(type) {
      this.type = type;
    },
    save() {
      addConfig(this.title, JSON.stringify(this.getConfig())).then((id) => {
        this.$refs.pageView.setTitle(this.title);
        this.$message.success("添加成功");
        this.type = "info";
        setTimeout(() => {
          this.$router.push(`/config/info/${id}`);
        }, 1000);
      });
    },
    editSave() {
      updateConfig(this.id, this.title, JSON.stringify(this.getConfig())).then(
        () => {
          this.$refs.pageView.setTitle(this.title);
          this.$message.success("修改成功");
        }
      );
    },
    deleteConfig() {
      removeConfig(this.id).then(() => {
        this.$message.success("删除成功");
        this.$router.push(`/config/index`);
      });
    },
    copy() {
      this.$router.push(`/config/add/${this.id}`);
      this.type = "add";
    },
    getConfig() {
      let config = { ...this.formData };
      const configKeys = [
        "typeMapperConfig",
        "entityConfig",
        "columnConfig",
        "mapperConfig",
        "controllerConfig",
      ];
      configKeys.forEach(key => {
        console.log(key)
        config[key] = this.$refs[key].getConfig().config;
      })
      console.log(config)
      return config;
    },
  },
};
</script>

<style scoped lang="less">
/deep/ .config-item {
  .item-label {
    color: rgba(0, 0, 0, 0.85);
    font-size: 16px;
  }

  .item-view {
    padding-top: 10px;

    .ant-form-item-label > label {
      color: #606266;
    }
  }

  .ant-form-item {
    margin-bottom: 0px;
  }
}

.config-item + .config-item {
  padding-top: 10px;
}
</style>