<template>
  <div class="config-view">
    <a-row class="item-view" :gutter="[16,16]">
      <a-col :span="24">
        <a-form-item label="公共字段">
          <a-input v-model="formData.commonColumns" placeholder="默认：create_time,update_time,create_user_id，update_user_id,delete_flag" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="隐藏字段">
          <a-input v-model="formData.hiddenColumns" placeholder="隐藏字段将不会被生成" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <span style="color: rgba(0, 0, 0, 0.85);font-weight: bold">MyBatis-Plus特性</span>
        <a-popover placement="topLeft">
          <template slot="content">
            <div class="qa-content">
              <div>以下配置为MyBatis-Plus的特性，需要在<span class="code">Mapper配置</span>中设置风格为MyBatis-Plus才有效。</div>
              <div>Insert：会生成<span class="code">@TableField(fill = FieldFill.INSERT)</span>注解，在执行insert语句时会自动维护所配置的字段。</div>
              <div>Update：会生成<span class="code">@TableField(fill = FieldFill.UPDATE)</span>注解，在执行update语句时会自动维护所配置的字段。</div>
              <div>Insert & Update：会生成<span class="code">@TableField(fill = FieldFill.INSERT_UPDATE)</span>注解，在执行insert或update语句时会自动维护所配置的字段。</div>
              <div>设置逻辑删除的字段，成员变量上会生成<span class="code">@TableLogic</span>注解。</div>
            </div>
          </template>
          <a-icon style="margin-left: 10px;cursor: pointer" type="alert" />
        </a-popover>
      </a-col>
      <a-col :span="24">
        <a-form-item label="逻辑删除">
          <a-input v-model="formData.logicDeleteColumns" placeholder="默认：delete_flag" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="Insert Fill">
          <a-input v-model="formData.insertFillColumns" placeholder="Insert时自动填充字段，默认：create_time,create_user_id" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="Update Fill">
          <a-input v-model="formData.updateFillColumns" placeholder="Update时自动填充字段" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="insert & Update Fill">
          <a-input v-model="formData.insertOrUpdateFillColumns" placeholder="Insert和Update时自动填充字段默认：update_time,update_user_id" :disabled="disabled"/>
        </a-form-item>
      </a-col>
    </a-row>

  </div>
</template>

<script>
import {defaultConfig} from "@/common/default-generator-config";
import ConfigView from "./config-view"
export default {
  extends:ConfigView,
  name: "column-config",
  data(){
    return {
      formData:defaultConfig.columnConfig
    }
  },
  created() {
    this.formData = {...this.config}
  },
  methods:{
    getConfig(){
      return {name: 'columnConfig', config: this.formData};
    }
  },
}
</script>

<style scoped lang="less">

</style>