<template>
  <div class="page">
    <div class="page-header">
      <!--    <a-breadcrumb>
            <a-breadcrumb-item v-for="(item,index) in $route.matched" :key="index">{{ item.name }}</a-breadcrumb-item>
          </a-breadcrumb>-->
      <a-page-header
          :title="title"
          :breadcrumb="{ props: { routes } }"
      >
        <template slot="extra">
          <slot name="header-bar"></slot>
        </template>
      </a-page-header>
    </div>
    <div class="page-body">
      <div class="body-view">
        <slot name="body"></slot>
      </div>

    </div>
    <div class="page-footer">

    </div>
  </div>
</template>

<script>
export default {
  name: "page",
  data() {
    return {
      routes: [],
      title: this.$route.name
    }
  },
  created() {
    this.initBreadcrumb();
  },
  methods: {
    initBreadcrumb() {
      let breadcrumb = [];
      this.$route.matched.forEach(item => {
        breadcrumb.push({
          path: item.path,
          breadcrumbName: item.name
        })
      })
      this.routes = breadcrumb
    },
    setTitle(title,breadcrumb) {
      this.title = title;
      if (breadcrumb){
        this.routes[this.routes.length - 1].breadcrumbName = title;
      }

    }
  }
}
</script>

<style scoped lang="less">
.page-header{
  background-color: #fff;
}
.page-body {
  padding: 20px;
  .body-view{
    background-color: #fff;
    padding: 16px;
    font-feature-settings: "tnum";
    position: relative;
    border-radius: 2px;
  }
}
</style>