import request from '@/common/utils/request'

const contextPath = ''
const module = '/config'

/**
 * 详情
 * @returns {AxiosPromise}
 */
export const configDetail = (id) => {
    return request({
        url: `${contextPath}${module}/${id}`,
        method: 'get',
    })
}


/**
 * 分页查询
 * @param pageSize
 * @param pageNo
 * @param keywords
 * @returns {AxiosPromise}
 */
export const configPage = (pageSize, pageNo, keywords) => {
    return request({
        url: `${contextPath}${module}/page`,
        method: 'get',
        params: {pageSize, pageNo, keywords}
    })
}

/**
 * 分页查询
 * @param keywords
 * @returns {AxiosPromise}
 */
export const configList = (keywords) => {
    return request({
        url: `${contextPath}${module}/list`,
        method: 'get',
        params: {keywords}
    })
}


/**
 * 新增
 * @param name
 * @param configContent
 */
export const addConfig = (name, configContent) => {
    return request({
        url: `${contextPath}${module}/save`,
        method: 'put',
        data: {name, configContent}
    })
}

/**
 * 修改
 * @param id
 * @param name
 * @param configContent
 * @returns {AxiosPromise}
 */
export const updateConfig = (id, name, configContent) => {
    return request({
        url: `${contextPath}${module}/update`,
        method: 'post',
        data: {id, name, configContent}
    })
}

/**
 * 删除
 * @param id
 * @param batch 是否批量删除
 * @returns {AxiosPromise}
 */
export const removeConfig = (id, batch) => {
    const url = `${contextPath}${module}/${batch ? 'delete/batch' : 'delete/' + id}`
    const data = batch ? {ids: arrToParam(id)} : null;
    return request({
        url,
        method: 'delete',
        params: data
    })
}


function arrToParam(arr){
    let str = '';
    arr.forEach(item => {
        str+=`,${item}`
    })
    return str.substr(1);
}
