<template>
  <page-view>
    <template slot="header-bar">
      <a-button icon="appstore" @click="toInfoView('info',-1)">默认配置</a-button>
      <a-button icon="plus" @click="toInfoView('add',null)">新增</a-button>
    </template>
    <div class="body" slot="body">
      <pl-table :columns="tableColumns" :load-method="loadData" size="small">
        <template slot="tableAction" slot-scope="{row}">
          <a-button type="link" size="small" @click="toInfoView('info',row.id)">详情</a-button>
          <!--          <a-button type="link" size="small">修改</a-button>
                    <a-button type="link" size="small">删除</a-button>-->
        </template>
      </pl-table>
    </div>
  </page-view>
</template>

<script>
const TABLE_COLUMNS = [
  {
    key: 'index',
    title: '序号',
    dataIndex: 'index',
    width: 55,
    align: 'center',
    scopedSlots: {customRender: 'index'}
  },
  {
    key: 'name',
    title: '名称',
    dataIndex: 'name',
    width: 180
  },
  {
    key: 'createTime',
    title: '创建时间',
    dataIndex: 'createTime',
    width: 180,
    ellipsis: true
  },
  {
    key: 'updateTime',
    title: '更新时间',
    dataIndex: 'updateTime',
    width: 180
  },
  {title: '操作', key: 'Action', scopedSlots: {customRender: 'Action'}, fixed: 'right', width: 280, align: 'center'}
]

import {configPage} from "@/apis/config"

const loadData = function (pagination) {
  return configPage(pagination.size, pagination.current, null)
}
export default {
  name: "index",
  data() {
    return {
      loadData,
      tableColumns: TABLE_COLUMNS
    }
  },
  methods: {
    toInfoView(type, id) {
      if (id) {
        this.$router.push(`/config/${type}/${id}`)
      } else {
        this.$router.push(`/config/${type}`)
      }

    }
  }
}
</script>

