<template>
  <div class="logo-view" :class="collapse ? 'center':''">
    <span v-for="(item,index) in logoAry" :key="index" :style="{color:getColor(index),fontSize:fontSize}">{{
        item
      }}</span>
  </div>
</template>

<script>
export default {
  name: "logo",
  props: {
    logo: {
      type: String,
      default: 'PL-Generator'
    },
    color: {
      type: Array,
      default() {
        return [
          '#409EFF',
          '#F56C6C',
          '#303133',
          '#409EFF',
          '#67C23A',
          '#E6A23C',
          '#F56C6C',
          '#303133',
          '#303133',
          '#303133',
          '#303133',
          '#303133',
          '#303133',
          '#303133',
          '#303133'
        ];
      }
    },
    defaultColor: {
      type: String,
      default: '#409EFF'
    },
    size: {
      type: [String, Number],
      default: '20px'
    },
    collapse: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    logoAry() {
      if (this.collapse) {
        return this.logo.split('').splice(0, 2);
      } else {
        return this.logo.split('');
      }
    },
    fontSize() {
      const size = this.size + '';
      if (size.indexOf('px')) {
        return size;
      } else {
        return size + 'px'
      }
    }
  },
  methods: {
    getColor(index) {
      if (index > this.color.length) {
        return this.defaultColor;
      } else {
        return this.color[index];
      }
    }
  }
}
</script>

<style scoped>
.logo-view {
  cursor: pointer;
  user-select: none;
}
.center{
  text-align: center;
}
</style>