<template>
  <div class="generate-view">
    <a-modal
        title="生成"
        :visible="visible"
        @cancel="handleCancel"
        width="800px"
        cancelText="取消"
        okText="确定"
    >
      <div class="generate-form">
        <a-row class="config-item">
          <a-row class="item-view" :gutter="[16, 16]">
            <a-col :span="12">
              <a-form-item label="配置项">
                <a-select
                    v-model="generateConfig.configId"
                    style="width: 100%"
                    placeholder="请选择配置，为空则根据默认配置生成"
                    @change="handleSelectConfigChange"
                >
                  <a-select-option
                      v-for="item in configList"
                      :value="item.id"
                      :key="item.id"
                  >{{ item.name }}
                  </a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item label="表前缀">
                <a-input
                    :required="true"
                    v-model="generateConfig.tablePrefix"
                    placeholder="生成时会清除表前缀"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item label="包名">
                <a-input
                    :required="true"
                    v-model="generateConfig.packageName"
                    placeholder="基础包名，默认：com.pl"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item label="作者">
                <a-input
                    :required="true"
                    v-model="generateConfig.author"
                    placeholder="默认：plserver generate"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item label="模块">
                <a-input
                    :required="true"
                    v-model="generateConfig.moduleName"
                    placeholder="生成时会在包名后追加模块名"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item label="注释">
                <a-input
                    :required="true"
                    v-model="generateConfig.codeAnnotation"
                    placeholder="注释"
                />
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="代码风格" class="generate-select">
                <a-radio-group name="radioGroup" v-model="generateConfig.style">
                  <a-radio value="pl-web"> PL-WEB</a-radio>
                  <a-radio value="mybatis-plus"> Mybatis-Plus</a-radio>
                  <a-radio value="mybatis"> Mybatis</a-radio>
                </a-radio-group>
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="是否装配">
                <a-switch v-model="generateConfig.wired"/>
                <a-popover placement="topLeft">
                  <template slot="content">
                    <div class="qa-content">
                      <div>
                        1.<span class="code">Servicer</span
                      >层将装配所对应的<span class="code">Mapper</span>。
                      </div>
                      <div>
                        2.<span class="code">Controller</span
                      >层将装配所对应的<span class="code">Service</span>。
                      </div>
                    </div>
                  </template>
                  <a-icon
                      style="margin-left: 10px; cursor: pointer"
                      type="alert"
                  />
                </a-popover>
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="装配方式">
                <a-radio-group
                    name="radioGroup"
                    v-model="generateConfig.wiredType"
                    :disabled="!generateConfig.wired"
                >
                  <a-radio value="resource"> @Resource</a-radio>
                  <a-radio value="autoWired"> @AutoWired</a-radio>
                  <a-radio value="constructor"> 构造方法</a-radio>
                </a-radio-group>
              </a-form-item>
            </a-col>
            <a-col :span="24">
              <a-form-item label="swagger">
                <a-switch v-model="generateConfig.swagger"/>
              </a-form-item>
            </a-col>

            <a-col :span="24">
              <a-form-item label="生成模板" class="generate-select">
                <a-checkbox-group
                    v-model="generateConfig.templates"
                    :options="templateList"
                />
              </a-form-item>
            </a-col>
          </a-row>
        </a-row>
      </div>
      <div class="dialog-footer" slot="footer">
        <a-button icon="eye" @click="generate">预览</a-button>
        <a-button type="primary" icon="download" @click="download">下载</a-button>
      </div>
    </a-modal>
    <code-dialog ref="codeDialog"></code-dialog>
  </div>
</template>

<script>
import {configList, configDetail} from "@/apis/config";
import {showCode, download} from "@/apis/generate";
import CodeDialog from "@/page/datasource/modules/code-dialog";

const templateList = [
  "Entity",
  "Mapper",
  "MapperXML",
  "Service",
  "ServiceImpl",
  "Controller",
];
export default {
  name: "generate-dialog",
  components: {CodeDialog},
  data() {
    return {
      visible: false,
      configList: [],
      templateList,
      generateConfig: {
        dsName: null,
        tableName: null,
        configId: null,
        packageName: null,
        author: null,
        tablePrefix: null,
        moduleName: null,
        codeAnnotation: null,
        style: "pl-web",
        wired: true,
        wiredType: "resource",
        swagger: true,
        templates: templateList,
      },
    };
  },
  methods: {
    show(ds, table) {
      this.generateConfig.dsName = ds;
      this.generateConfig.tableName = table;
      this.visible = true;
      this.getConfigList();
    },
    getConfigList() {
      configList().then((data) => {
        this.configList = data;
      });
    },
    generate() {
      showCode(this.generateConfig).then((data) => {
        this.$refs.codeDialog.show(data);
      });
    },
    download() {
      download(this.generateConfig)
    },
    handleCancel() {
      this.visible = false;
    },
    handleSelectConfigChange(e) {
      configDetail(e).then((data) => {
        const config = data.config;
        this.generateConfig.tablePrefix = config.tablePrefix;
        this.generateConfig.packageName = config.packageName;
        this.generateConfig.author = config.author;
        this.generateConfig.style = config.style;
        this.generateConfig.wired = config.wired;
        this.generateConfig.wiredType = config.wiredType;
        this.generateConfig.swagger = config.swagger;
      });
    },
  },
};
</script>

<style scoped lang="less">
/deep/ .generate-select {
  .ant-radio-wrapper,
  .ant-checkbox-wrapper {
    padding: 8px 15px 0 10px;
    border-radius: 3px;
    border: 1px solid #dcdfe6;
    box-sizing: border-box;
    height: 32px;
    color: #606266;
    font-weight: 500;
    line-height: 1;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    outline: none;
    font-size: 14px;
  }

  .ant-checkbox-wrapper {
    padding-right: 0;
  }

  .ant-radio-wrapper-checked,
  .ant-checkbox-wrapper-checked {
    border-color: #1991ff;
  }
}
</style>