import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import Layout from '@/layout'
import IndexView from '@/page/datasource'
import DataSourceInfoView from '@/page/datasource/modules/info'
import TableDesign from '@/page/datasource/modules/table-design'
import ConfigIndexView from '@/page/config/index'
import ConfigInfoView from '@/page/config/modules/info'

const routes = [
    {
        path: '/',
        name: '首页',
        component: Layout,
        redirect: '/datasource/index'
    },
    {
        path: '/datasource',
        name: '数据源',
        component: Layout,
        redirect: '/datasource/index',
        children:[
            {
                path: 'index',
                name: '数据源列表',
                component: IndexView
            },
            {
                path: ':id',
                name: '数据源详情',
                component: DataSourceInfoView
            },
            {
                path: 'design/:id',
                name: '表设计',
                component: TableDesign
            }
        ]
    },
   {
        path: '/config',
        name: '配置',
        component: Layout,
        redirect: '/config/index',
        children:[
            {
                path: 'index',
                name: '配置列表',
                component: ConfigIndexView
            },
            {
                path: ':type/:id',
                name: '配置详情',
                component: ConfigInfoView
            },
            {
                path: 'add',
                name: '新增配置',
                component: ConfigInfoView
            }
        ]
    }
]

const router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    /* 路由发生变化修改页面title */
    if (to.name) {
        document.title = to.name;
    }
    next();
})
export default router