<template>
  <a-modal
      :title="datasource.id != null ? '修改数据源' : '新增数据源'"
      :visible="visible"
      width="50%"
      okText="保存"
      cancelText="取消"
      @cancel="cancel"
      @ok="bindOnSave"
  >
    <div class="edit-form pl-form-line">
      <a-form-item label="名称">
        <a-input :required="true" v-model="datasource.name" placeholder="请输入数据源名称"/>
      </a-form-item>
      <a-form-item label="配置项">
        <a-select
            v-model="datasource.dbType"
            style="width: 100%"
            placeholder="请选择配置，为空则根据默认配置生成"
        >
          <a-select-option
              v-for="item in dbTypes"
              :value="item.id"
              :key="item.id"
              :disabled="item.disable"
          >{{ item.name }}
          </a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item label="连接方式">
        <a-radio-group name="radioGroup" v-model="datasource.connectionType" :default-value="0">
          <a-radio :value="0">主机连接</a-radio>
          <a-radio :value="1">URL连接</a-radio>
        </a-radio-group>
      </a-form-item>
      <div class="connection-host" v-if="datasource.connectionType == '0'">
        <a-form-item label="主机IP">
          <a-input :required="true" v-model="datasource.host" placeholder="请输入主机IP"/>
        </a-form-item>
        <a-form-item label="端口">
          <a-input :required="true" v-model="datasource.port" placeholder="请输入端口"/>
        </a-form-item>
        <a-form-item label="数据库">
          <a-input :required="true" v-model="datasource.dbName" placeholder="请输入数据库名称"/>
        </a-form-item>
      </div>

      <div class="connection-url" v-else>
        <a-form-item label="连接地址">
          <a-textarea :auto-size="{ minRows: 4, maxRows: 6 }" :required="true" v-model="datasource.url"
                      placeholder="请输入连接地址"/>
        </a-form-item>
      </div>

      <a-form-item label="用户名">
        <a-input :required="true" v-model="datasource.username" placeholder="请输入用户名"/>
      </a-form-item>

      <a-form-item label="密码">
        <a-input type="password" :required="true" v-model="datasource.password" placeholder="请输入密码"/>
      </a-form-item>
    </div>
  </a-modal>
</template>

<script>

import {add, update} from "@/apis/datasource";
import {message} from "ant-design-vue";

const defalutDatasource = {
  "id": null,
  "name": null,
  "connectionType": 0,
  "username": null,
  "password": null,
  "dbType": 'mysql',
  "host": null,
  "port": '3306',
  "instance": null,
  "dbName": null,
  "url": null
}
const dbTypes = [
  {id: "mysql", name: 'MySQL', disable: false},
  {id: "pg", name: 'pg', disable: true},
  {id: "sqlserver", name: 'SqlServer', disable: true},
  {id: "oracle", name: 'oracle', disable: true},
  {id: "db2", name: 'db2', disable: true}
]
export default {
  name: "edit-form",
  data() {
    return {
      dbTypes,
      visible: false,
      form: this.$form.createForm(this, {name: 'coordinated'}),
      datasource: {...defalutDatasource}
    }
  },
  methods: {
    show(datasource) {
      if (datasource) {
        this.datasource = datasource;
      }
      this.visible = true;
    },
    bindOnSave() {
      const form = {...this.datasource}
      if (form.id) {
        update(form).then(() => {
          this.saveSuccess();
        })
      } else {
        add(form).then(() => {
          this.saveSuccess();
        })
      }
    },
    cancel() {
      this.datasource = {...defalutDatasource}
      this.visible = false;
      this.$emit("change")
    },
    saveSuccess() {
      message.success({
        content: "成功"
      })
      this.cancel();
    }
  }
}
</script>

