<template>
  <a-layout class="layout">
    <a-layout-sider theme="light" width="240" class="layout-sider-left" :collapsible="true" :collapsed="collapsed">
      <a-layout-header class="ant-layout-sider-light log-group">
        <logo :collapse="collapsed"></logo>
      </a-layout-header>
      <div class="datasource-list">
        <a-menu mode="inline" :selected-keys="selectIds" style="width: 100%" :inline-collapsed="collapsed"
                @select="bindOnSelect">
          <a-menu-item key="datasource">
            <span>
              <a-icon type="folder"/>
              <span>所有数据源</span>
            </span>
          </a-menu-item>
          <a-menu-item v-for="ds in datasourceList" :key="ds.id+''">
            <span>
              <a-icon type="database"/>
              <span>{{ ds.name }}</span>
            </span>
          </a-menu-item>
        </a-menu>
      </div>
    </a-layout-sider>
    <a-layout>
      <a-layout-header class="ant-layout-sider-light layout-header">
        <div class="header-view">
          <div class="header-left">
            <a-button type="link" :icon="collapsed ? 'menu-unfold' : 'menu-fold'" size="small"
                      @click="bindOnCollapsed"/>
          </div>
          <div class="header-body">
            <!--            <a-input-group compact>
                          <a-input class="search-input" v-model="keywords" placeholder="搜索" >
                            <a-icon slot="prefix" type="search"/>
                          </a-input>
                          <a-date-picker style="width: 50%" />
                        </a-input-group>-->

            <a-auto-complete
                class="ds-search"
                :dropdown-match-select-width="false"
                size="large"
                placeholder="搜索"
                option-label-prop="title"
                @select="bindOnSeachSelect"
            >
              <template slot="dataSource">
                <a-select-option v-for="ds in searchDatasource" :key="ds.id" :value="ds.id+''" :title="ds.name">
                  {{ds.name}}
                </a-select-option>
              </template>
              <a-input class="search-input" v-model="keywords" placeholder="搜索">
                <a-icon slot="prefix" type="search"/>
              </a-input>
            </a-auto-complete>
          </div>
          <div class="header-right">
            <a-button shape="circle" icon="database" size="small" @click="toView('/datasource/index')"/>
            <a-button shape="circle" icon="setting" size="small" @click="toView('/config/index')"/>
          </div>
        </div>
      </a-layout-header>
      <a-layout-content class="layout-body">
        <router-view></router-view>
      </a-layout-content>
    </a-layout>
  </a-layout>
</template>

<script>
import logo from "@/components/logo";
import {mapState} from 'vuex'

export default {
  name: "index",
  components: {
    logo
  },
  computed: {
    ...mapState(['datasourceList']),
    searchDatasource() {
      if (!this.datasourceList) {
        return [];
      }
      const allDs = [...this.datasourceList]

      if (this.keywords) {
        console.log(allDs.filter(item => item.name.indexOf(this.keywords) != -1))
        return allDs.filter(item => item.name.indexOf(this.keywords) != -1)
      } else {
        return allDs;
      }
    }
  },
  data() {
    return {
      collapsed: false,
      keywords: null,
      headerBarVisible: false,
      selectIds: ['datasource']
    }
  },
  created() {
    this.$store.dispatch('refreshDsMenus');
  },
  methods: {
    bindOnCollapsed() {
      this.collapsed = !this.collapsed;
    },
    toView(key) {
      if (this.$route.path != key)
        this.$router.push(key)
    },
    bindOnSelect({key}) {
      if (key == 'datasource') {
        this.$router.push({
          path: `/datasource/index`
        })
        this.selectIds[0] = 'datasource'
        return;
      }
      const ds = this.datasourceList.filter(item => item.id == key)
      if (ds) {
        this.$router.push({
          path: `/datasource/${key}`,
          query: {
            dsName: ds[0].name
          }
        })
      }

    },
    bindOnSeachSelect(e) {
      this.bindOnSelect({key: e})
    },
    setActiveMenu() {
      if (this.$route.path.startsWith('/datasource')) {
        const {id} = this.$route.params
        if (id) {
          this.selectIds[0] = id
        } else if (this.$route.path == '/datasource/index') {
          this.selectIds[0] = 'datasource'
        } else {
          this.selectIds[0] = ''
        }
      } else {
        this.selectIds[0] = ''
      }
    }
  },
  watch: {
    $route: {
      handler() {
        this.setActiveMenu();
      },

      immediate: true
    }
  }
}
</script>

<style scoped lang="less">
.layout {
  height: 100%;

  .layout-sider-left {
    box-shadow: 2px 0 6px #00152914;
    position: relative;
    z-index: 10;

    .ant-menu-inline, .ant-menu-vertical {
      border-right: none;
    }

    .log-group {
      padding: 0 24px;
    }
  }

  .layout-header {
    box-shadow: 0 1px 4px #00152914;
    position: relative;
    z-index: 9;
    padding: 0 24px;

    .header-view {
      width: 100%;
      display: flex;

      div + div {
        padding-left: 20px;
      }

      .header-body {
        flex: 1;

        .ds-search {
          width: 300px;

          .search-input {
            width: 300px;

            /deep/ .ant-input-prefix {
              color: #c2c2c2;
            }

            /deep/ .ant-input {
              border: none;

              &:focus, &:active {
                border: none;
                outline: none;
                box-shadow: none;
              }
            }

          }
        }

      }
    }
  }

  .layout-body {

  }
}
</style>