<script>
export default {
  name: "config-view",
  props: {
    disabled: {
      type: Boolean,
      default: true
    },
    config: {
      type: Object,
      default() {
        return null
      }
    }
  },
  watch: {
    config: {
      deep: true,
      handler(val) {
        this.formData = {...val}
      }
    }
  }
}
</script>

