<template>
  <div class="config-view entity-config">
    <a-row class="item-view" :gutter="[16,16]">

      <a-col :span="24">
        <a-form-item label="开启lombok">
          <a-switch v-model="formData.lombokConfig.enable" :disabled="disabled"/>
          <div class="lombox-checkbox-group">
            <a-checkbox v-model="formData.lombokConfig.builder" :disabled="!formData.lombokConfig.enable || disabled">
              @Builder
            </a-checkbox>
            <a-checkbox v-model="formData.lombokConfig.data" :disabled="!formData.lombokConfig.enable || disabled">
              @Data
            </a-checkbox>
            <a-checkbox v-model="formData.lombokConfig.getter" :disabled="!formData.lombokConfig.enable || disabled">
              @Getter
            </a-checkbox>
            <a-checkbox v-model="formData.lombokConfig.setter" :disabled="!formData.lombokConfig.enable || disabled">
              @Setter
            </a-checkbox>
            <a-checkbox v-model="formData.lombokConfig.toStr" :disabled="!formData.lombokConfig.enable || disabled">
              @ToString
            </a-checkbox>
            <a-checkbox v-model="formData.lombokConfig.equalsAndHashCode"
                        :disabled="!formData.lombokConfig.enable || disabled">@EqualsAndHashCode
            </a-checkbox>
            <a-checkbox v-model="formData.lombokConfig.allArgsConstructor"
                        :disabled="!formData.lombokConfig.enable || disabled">@AllArgsConstructor
            </a-checkbox>
            <a-checkbox v-model="formData.lombokConfig.noAllArgsConstructor"
                        :disabled="!formData.lombokConfig.enable || disabled">@NoAllArgsConstructor
            </a-checkbox>
          </div>
        </a-form-item>
      </a-col>

      <a-col :span="24">
        <a-form-item label="继承父类">
          <a-switch v-model="formData.extendsSuper" :disabled="disabled"/>
          <a-popover placement="topLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.继承父类并配置公共字段后，Entity中将不在生成所配置公共字段。</div>
                <div>2.如果<span class="code">Mapper配置</span>中设置了风格为<span class="code">PL-WEB</span>,父类将继承<span
                    class="code">AbstractPlEntity</span>。
                </div>
                <div>3.如果<span class="code">Mapper配置</span>中设置了风格为<span class="code">MyBatis-Plus</span>,父类将继承<span
                    class="code">Model</span>。
                </div>
              </div>
            </template>
            <a-icon style="margin-left: 10px;cursor: pointer" type="alert"/>
          </a-popover>
        </a-form-item>
      </a-col>

      <a-col :span="24">
        <a-form-item label="父类包名">

          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.代码风格为：<span class="code">PL-WEB</span>，默认为：<span class="code">com.pl.data.core.entity.po.AbstractPlEntity</span>。
                </div>
                <div>2.代码风格为：<span class="code">MyBatis-Plus</span>，默认为：<span class="code">com.baomidou.mybatisplus.extension.activerecord.Model</span>。
                </div>
                <div>3.代码风格为：<span class="code">MyBatis</span>，默认为空。</div>
              </div>
            </template>
            <a-input v-model="formData.superClassPackage" placeholder="请输入父类包名" :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>

      <a-col :span="24">
        <a-form-item label="父类泛型包名">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.默认为空,如果Mapper风格为<span class="code">PL-WEB</span>或<span class="code">MyBatis-Plus</span>，默认为：<span
                    class="code">this</span>,自定义时，多个用","隔开'。
                </div>
                <div>2.<span class="code">this</span>等于<span class="code">Entity</span>的类名。</div>
              </div>
            </template>
            <a-textarea v-model="formData.superClassGenericPackage" :auto-size="{ minRows: 4, maxRows: 6 }"
                        placeholder='默认为空，如果使用Mybatis-Plus，默认为：this,this代表当前类,多个用","隔开' :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>

    </a-row>
  </div>
</template>

<script>

import {defaultConfig} from "@/common/default-generator-config";
import ConfigView from "./config-view"

export default {
  extends:ConfigView,
  name: "entity-config",
  data() {
    return {
      formData: defaultConfig.entityConfig
    }
  },
  methods: {
    render(data) {
      if (data) {
        this.formData = data;
      }
    },
    getConfig() {
      return {name: 'entityConfig', config: this.formData};
    }
  }
}
</script>

<style scoped lang="less">
.lombox-checkbox-group {
  display: inline-block;
  padding: 0 10px;
}


</style>