<template>
  <a-modal
      title="生成"
      :visible="visible"
      @cancel="handleCancel"
      width="80%"
  >
    <a-tabs default-active-key="entityCode">
      <a-tab-pane v-if="code.entity" key="entityCode" tab="Entity">
        <code-editor mode-type="java" id="entityCode" ref="entityCode" :value="code.entity"></code-editor>
      </a-tab-pane>

      <a-tab-pane v-if="code.mapperXml" key="mapperCode" tab="Mapper">
        <code-editor mode-type="java" id="mapperCode" ref="mapperCode" :value="code.mapper"></code-editor>
      </a-tab-pane>

      <a-tab-pane v-if="code.mapperXml" key="mapperXmlCode" tab="MapperXML">
        <code-editor mode-type="xml" id="mapperXmlCode" ref="mapperXmlCode" :value="code.mapperXml"></code-editor>
      </a-tab-pane>

      <a-tab-pane v-if="code.service" key="serviceCode" tab="Service">
        <code-editor mode-type="java" id="serviceCode" ref="serviceCode" :value="code.service"></code-editor>
      </a-tab-pane>

      <a-tab-pane v-if="code.serviceImpl" key="serviceImplCode" tab="ServiceImpl">
        <code-editor mode-type="java" id="serviceImplCode" ref="serviceImplCode" :value="code.serviceImpl"></code-editor>
      </a-tab-pane>

      <a-tab-pane v-if="code.controller" key="controllerCode" tab="Controller">
        <code-editor mode-type="java" id="controllerCode" ref="controllerCode" :value="code.controller"></code-editor>
      </a-tab-pane>
    </a-tabs>
  </a-modal>
</template>

<script>
import CodeEditor from "@/code-editor"

export default {
  name: "code-dialog",
  components: {
    CodeEditor
  },
  data() {
    return {
      visible: false,
      code: {
        entity: null,
        mapper: null,
        mapperXml: null,
        service: null,
        serviceImpl: null,
        controller: null
      }
    }
  },
  methods: {
    show(data) {
      this.code = data;
      this.visible = true;
    },
    handleCancel() {
      this.visible = false;
    }
  }
}
</script>

<style scoped>

</style>