<template>
  <page-view ref="pageView">
    <template slot="header-bar">
      <a-button-group>
        <a-button icon="plus">创建表</a-button>
        <a-button icon="bars" @click="changeCommonFieldVisible">公共字段</a-button>
      </a-button-group>
    </template>
    <div class="body" slot="body">
      <pl-table :columns="TABLE_COLUMNS" :load-method="loadData" size="small" :pageData="{dsName:ds}"
                row-key="tableName">
        <template slot="tableAction">
          <a-button type="link" size="small">表结构设计</a-button>
        </template>
      </pl-table>


      <a-drawer
          title="公共字段"
          placement="right"
          :closable="false"
          width="420px"
          :visible="commonFieldVisible"
          @close="changeCommonFieldVisible"
      >
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </a-drawer>
    </div>
  </page-view>
</template>

<script>
import {tablePage} from "@/apis/table";
import {info} from "@/apis/datasource";

const loadData = function (pagination, data) {
  return tablePage(pagination.size, pagination.current, data.dsName, null)
}
const TABLE_COLUMNS = [
  {
    key: 'index',
    title: '序号',
    dataIndex: 'index',
    width: 55,
    align: 'center',
    scopedSlots: {customRender: 'index'}
  },
  {
    key: 'tableName',
    title: '表名',
    dataIndex: 'tableName',
    width: 180
  },
  {
    key: 'tableComment',
    title: '注释',
    dataIndex: 'tableComment',
    width: 300,
    ellipsis: true
  },
  {
    key: 'createTime',
    title: '创建时间',
    dataIndex: 'createTime',
    width: 180
  },
  {title: '操作', key: 'Action', scopedSlots: {customRender: 'Action'}, fixed: 'right', width: 280, align: 'center'}
]
export default {
  name: "table-design",
  data() {
    return {
      loadData,
      TABLE_COLUMNS,
      id: null,
      ds: null,
      commonFieldVisible: false
    }
  },
  created() {
    this.id = this.$route.params.id;
    this.ds = this.$route.query.dsName;
    this.getDsInfo()
  },
  methods: {
    changeCommonFieldVisible(){
      this.commonFieldVisible = !this.commonFieldVisible;
    },
    getDsInfo() {
      info(this.id).then(data => {
        this.ds = data.name;
        this.$refs.pageView.setTitle(data.name);
      })
    }
  }
}
</script>

<style scoped>

</style>