import request from '@/common/utils/request'

const contextPath = ''
const module = '/table'


/**
 * 分页查询
 * @param pageSize
 * @param pageNo
 * @param dsName
 * @param tableName
 * @returns {AxiosPromise}
 */
export const tablePage = (pageSize, pageNo, dsName, tableName) => {
    return request({
        url: `${contextPath}${module}/page`,
        method: 'get',
        params: {pageSize, pageNo, dsName, tableName}
    })
}