<template>
  <page-view>
    <template slot="header-bar">
      <a-button icon="plus" @click="addDatasource">新增</a-button>
<!--      <a-button type="danger">删除</a-button>-->
    </template>
    <div class="body" slot="body">
      <a-table size="small" table-layout="fixed" :columns="tableColumns" :data-source="tableData"  :pagination="pagination" :loading="loading" rowKey="id" :bordered="false">
    <span slot="index" slot-scope="text, record, index"> {{
        (pagination.current - 1) * pagination.size + parseInt(index) + 1
      }} </span>
        <span slot="connectionType" slot-scope="text, record">{{record.connectionType == 0 ? '主机连接' : 'URL连接'}}</span>

        <template slot="Action" slot-scope="record">
          <a-button type="link" size="small" @click="openDatasource(record)">进入数据源</a-button>
<!--          <a-button type="link" size="small" @click="openTableDesign(record)">表设计</a-button>-->
          <a-button type="link" size="small" @click="updateDatasource(record)">修改</a-button>
          <a-button type="link" size="small">删除</a-button>
        </template>
      </a-table>
      <edit-form ref="editForm" @change="bindOnEditChange"></edit-form>
    </div>
  </page-view>
</template>

<script>

const TABLE_COLUMNS = [
  {
    key: 'index',
    title: '序号',
    dataIndex: 'index',
    width: 55,
    align: 'center',
    scopedSlots: {customRender: 'index'}
  },
  {
    key: 'name',
    title: '名称',
    dataIndex: 'name',
    width: 180
  },
  {
    key: 'connectionType',
    title: '连接方式',
    dataIndex: 'connectionType',
    scopedSlots: { customRender: 'connectionType' },
    width: 180
  },
  {
    key: 'url',
    title: 'URL',
    dataIndex: 'url',
    width: 300,
    ellipsis: true
  },
  {
    key: 'username',
    title: '用户名',
    dataIndex: 'username',
    width: 180
  },
 /* {
    key: 'password',
    title: '密码',
    dataIndex: 'password',
    width: 180
  },*/
  {title: '操作', key: 'Action', scopedSlots: {customRender: 'Action'}, fixed: 'right', width: 280, align: 'center'}
]
const defaultPagination = {
  showTotal: total => `共有 ${total} 条数据`,  //分页中显示总的数据
  total: 0,
  size: "10",
  current: 1
}
import {page} from "@/apis/datasource"
import EditForm from "@/page/datasource/modules/edit-form";


export default {
  name: "index",
  components: {EditForm},
  data() {
    return {
      pagination:defaultPagination,
      tableColumns: TABLE_COLUMNS,
      tableData:[],
      loading:false
    }
  },
  mounted() {
    this.loadData()
  },
  methods: {
    loadData(){
      this.loading = true;
      page(this.pagination.size, this.pagination.current, null).then(res => {
        this.loading = false;
        this.tableData = res.records;
        this.pagination.current = res.current;
        this.pagination.total = res.total;
      }).catch(()=>{
        this.loading = false;
      })
    },
    openDatasource(ds) {
      this.$router.push({
        path: `/datasource/${ds.id}`,
        query: {
          dsName: ds.name
        }
      })
    },
    openTableDesign(ds) {
      this.$router.push({
        path: `/datasource/design/${ds.id}`,
        query: {
          dsName: ds.name
        }
      })
    },
    addDatasource(){
      this.$refs.editForm.show();
    },
    updateDatasource(row){
      this.$refs.editForm.show(row);
    },
    bindOnEditChange(){
      this.loadData();
    }
  }
}
</script>

