<template>
  <page-view ref="pageView">
    <div class="body" slot="body">
      <pl-table ref="dsTableList" :columns="tableColumns" :load-method="loadData" size="small" :pageData="{dsName:ds}" row-key="tableName">
        <template slot="tableAction" slot-scope="{row}">
          <a-button type="link" size="small" @click="bindOnShowGenerate(row)">生成代码</a-button>
        </template>
      </pl-table>
      <generate-dialog ref="generateDialog"></generate-dialog>
    </div>
  </page-view>
</template>

<script>
import {info} from "@/apis/datasource"
import {tablePage} from "@/apis/table"
import GenerateDialog from "@/page/datasource/modules/generate-dialog";

const loadData = function (pagination, data) {
  return tablePage(pagination.size, pagination.current, data.dsName, null)
}
const TABLE_COLUMNS = [
  {
    key: 'index',
    title: '序号',
    dataIndex: 'index',
    width: 55,
    align: 'center',
    scopedSlots: {customRender: 'index'}
  },
  {
    key: 'tableName',
    title: '表名',
    dataIndex: 'tableName',
    width: 180
  },
  {
    key: 'tableComment',
    title: '注释',
    dataIndex: 'tableComment',
    width: 300,
    ellipsis: true
  },
  {
    key: 'createTime',
    title: '创建时间',
    dataIndex: 'createTime',
    width: 180
  },
  {title: '操作', key: 'Action', scopedSlots: {customRender: 'Action'}, fixed: 'right', width: 280, align: 'center'}
]
export default {
  name: "datasource-info",
  components: {GenerateDialog},
  data() {
    return {
      loadData,
      id: null,
      ds: null,
      tableColumns: TABLE_COLUMNS
    }
  },
  created() {
    this.id = this.$route.params.id;
    this.ds = this.$route.query.dsName;
    this.getDsInfo();
  },
  methods: {
    getDsInfo() {
      info(this.id).then(data => {
        this.ds = data.name;
        this.$refs.pageView.setTitle(data.name);
      })
    },
    bindOnShowGenerate(row){
      this.$refs.generateDialog.show(this.ds,row.tableName)
    }
  },
  watch: {
    $route: {
      handler () {
        const { id } = this.$route.params
        const { dsName } = this.$route.query
        this.id = id;
        this.ds = dsName;
        this.getDsInfo();
        if (this.$refs.dsTableList){
          this.$refs.dsTableList.loadData();
        }

        
        // 更新数据
      },

      immediate: true
    }
  }
}
</script>

