import request from '@/common/utils/request'

const contextPath = ''
const module = '/datasource'

/**
 * 分页查询数据源
 * @param pageSize
 * @param pageNo
 * @param keywords
 * @returns {AxiosPromise}
 */
export const page = (pageSize, pageNo, keywords) => {
    return request({
        url: `${contextPath}${module}/page`,
        method: 'get',
        params: {pageSize, pageNo, keywords}
    })
}

/**
 * 获取数据源列表
 * @param keywords
 * @returns {AxiosPromise}
 */
export const list = (keywords) => {
    return request({
        url: `${contextPath}${module}/list`,
        method: 'get',
        params: {keywords}
    })
}


/**
 * 新增数据源
 * @param data
 */
export const add = (data) => {
    return request({
        url: `${contextPath}${module}/add`,
        method: 'put',
        params: data
    })
}

/**
 * 修改数据源
 * @param data
 * @returns {AxiosPromise}
 */
export const update = (data) => {
    return request({
        url: `${contextPath}${module}/update`,
        method: 'post',
        params: data
    })
}

/**
 * 删除
 * @param id
 * @param batch 是否批量删除
 * @returns {AxiosPromise}
 */
export const remove = (id, batch) => {
    const url = `${contextPath}${module}/${batch ? 'delete/batch' : id}`
    const data = batch ? {ids: arrToParam(id)} : null;
    return request({
        url,
        method: 'delete',
        params: data
    })
}
/**
 * 数据源详情
 * @param keywords
 * @returns {AxiosPromise}
 */
export const info = (id) => {
    return request({
        url: `${contextPath}${module}/${id}`,
        method: 'get'
    })
}

function arrToParam(arr){
    let str = '';
    arr.forEach(item => {
        str+=`,${item}`
    })
    return str.substr(1);
}
