<template>
  <div class="config-view">
    <a-row class="item-view" :gutter="[16,16]">
      <a-col :span="24">
        <a-form-item label="生成API">
          <a-switch v-model="formData.basicApi" :disabled="disabled"/>
          <a-popover placement="topLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.开启后将生成
                  <span class="code">save</span>、
                  <span class="code">remove</span>、
                  <span class="code">update</span>、
                  <span class="code">getById</span>、
                  <span class="code">page</span>、
                  <span class="code">list</span>等接口方法
                </div>
              </div>
            </template>
            <a-icon style="margin-left: 10px;cursor: pointer" type="alert"/>
          </a-popover>
        </a-form-item>
      </a-col>

      <a-col :span="24">
        <a-form-item label="响应实体包名">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.代码风格为：<span class="code">PL-WEB</span>，默认为：<span class="code">com.pl.core.response.Result</span>。
                </div>
              </div>
            </template>
            <a-input v-model="formData.responseEntity.packageName" placeholder="请输入响应实体包名" :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>
      <a-col :span="12">
        <a-form-item label="成功方法">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.如成功返回为：<span class="code">Result.ok(data)</span>则填写：<span class="code">ok</span>。</div>
              </div>
            </template>
            <a-input v-model="formData.responseEntity.successMethod" placeholder="请输入成功方法" :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>


      <a-col :span="12">
        <a-form-item label="失败方法">
          <a-popover placement="bottomLeft">
            <template slot="content">
              <div class="qa-content">
                <div>1.如失败返回为：<span class="code">Result.fail(data)</span>则填写：<span class="code">fail</span>。</div>
              </div>
            </template>
            <a-input v-model="formData.responseEntity.errorMethod" placeholder="请输入失败方法" :disabled="disabled"/>
          </a-popover>
        </a-form-item>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import {defaultConfig} from "@/common/default-generator-config";
import ConfigView from "./config-view"

export default {
  extends:ConfigView,
  name: "controller-config",
  data() {
    return {
      formData: defaultConfig.controllerConfig
    }
  },
  methods: {
    getConfig() {
      return {name: 'controllerConfig', config: this.formData};
    }
  }
}
</script>

<style scoped>

</style>