import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

import {list} from '@/apis/datasource'
const store = new Vuex.Store({
    state: {
        // 数据源
        datasourceList: null
    },
    mutations: {
        SET_DATASOURCE_LIST(state,list){
            state.datasourceList = list;
        }
    },
    actions:{
        // 刷新数据源
        refreshDsMenus({commit}){
            list().then(data => {
                commit('SET_DATASOURCE_LIST',data)
            })
        }
    }
})

export default store