<template>
  <a-table table-layout="fixed" :columns="columns" :data-source="tableData" :size="size" :pagination="pagination" :loading="loading" :rowKey="rowKey" :bordered="bordered" @change="bindOnChange">
    <span v-if="showIndex" slot="index" slot-scope="text, record, index"> {{
        (pagination.current - 1) * pagination.size + parseInt(index) + 1
      }} </span>
    <div slot="Action" slot-scope="text, record">
      <slot name="tableAction" :row="record">
      </slot>
    </div>
    <slot name="plColumns">
    </slot>
  </a-table>
</template>

<script>
const defaultPagination = {
  showTotal: total => `共有 ${total} 条数据`,  //分页中显示总的数据
  total: 0,
  size: "10",
  current: 1
}
export default {
  name: "pl-table",
  props: {
    columns: {
      type: Array,
      default: null
    },
    page: {
      type: Boolean,
      default: true
    },
    showIndex: {
      type: Boolean,
      default: true
    },
    loadMethod: {
      type: Function,
      default: null
    },
    rowKey: {
      type: String,
      default: 'id'
    },
    size: {
      type: String,
      default: 'default'
    },
    bordered: {
      type: Boolean,
      default: false
    },
    pageData: {
      type: [Object,Array,String,Number],
      default: null
    },
    height: {
      type: String,
      default: '560'
    },
  },
  data() {
    return {
      tableData: [],
      pagination: defaultPagination,
      loading: false
    }
  },
  mounted() {
    this.loadData();
  },
  methods:{
    loadData() {
      if (this.loadMethod) {
        this.loading = true;
        this.loadMethod(this.pagination,this.pageData).then(data => {
          this.tableData = data.records;
          this.pagination.current = data.current;
          this.pagination.total = data.total;
          this.loading = false;
        }).catch(()=>{

          this.loading = false;
        })
      }
    },
    bindOnChange(e){
      if (e.current != this.pagination.current || e.size != this.pagination.size){
        this.pagination.current = e.current;
        this.pagination.size = e.size
        this.loadData();
      }
    }
  }
}
</script>

<style scoped lang="less">
/deep/.ant-table{
  .ant-table-thead > tr > th {
    color: #909399;
    font-weight: 500;
  }
}
/deep/.ant-table-small{
  border: none;
}

</style>