<template>
  <div class="config-view mapper-config">
    <a-row class="item-view" :gutter="[16,16]">
      <a-col :span="24">
        <a-form-item label="添加@Mapper注解">
            <a-switch v-model="formData.mapperAnnotation" :disabled="disabled"/>
        </a-form-item>
      </a-col>
      <a-col :span="24">
        <a-form-item label="生成XML">
            <a-switch v-model="formData.buildXml" :disabled="disabled"/>
        </a-form-item>
      </a-col>
    </a-row>
  </div>
</template>

<script>

import {defaultConfig} from "@/common/default-generator-config";
import ConfigView from "./config-view"

export default {
  extends:ConfigView,
  name: "mapper-config",
  data() {
    return {
      formData: defaultConfig.mapperConfig
    }
  },
  methods: {
    getConfig() {
      return {name: 'mapperConfig', config: this.formData};
    }
  }
}
</script>

<style scoped>
.ant-form-item-label {
  min-width: 180px !important;
}
</style>