<template>
  <div class="config-view">
    <div class="view-header" v-if="!disabled">
      <a-button type="primary" size="small" icon="plus" @click="addRow"
        >新增</a-button
      >
    </div>
    <a-table
      :columns="columns"
      :data-source="tableData"
      rowKey="columnType"
      :pagination="false"
    >
      <span slot="index" slot-scope="text, record, index">
        {{ parseInt(index) + 1 }}
      </span>

      <div slot="columnType" slot-scope="text, record">
        <a-input
          v-if="record.edit"
          style="margin: -5px 0"
          v-model="editRow.columnType"
        />
        <template v-else> {{ record.columnType }}</template>
      </div>

      <div slot="javaType" slot-scope="text, record">
        <a-input
          v-if="record.edit"
          style="margin: -5px 0"
          v-model="editRow.javaType"
        />
        <template v-else> {{ record.javaType }}</template>
      </div>
      <span slot="importPackage" slot-scope="text, record">
        <a-switch
          v-if="record.edit"
          :checked="editRow.importPackage"
          @change="importPackageChange"
        />
        <template v-else> {{ record.importPackage }}</template>
      </span>
      <div slot="packageName" slot-scope="text, record">
        <a-input
          v-if="record.edit"
          style="margin: -5px 0"
          v-model="editRow.packageName"
        />
        <template v-else> {{ record.packageName }}</template>
      </div>
      <template slot="Action" slot-scope="text, record, index">
        <a-button
          v-if="!record.edit"
          type="link"
          size="small"
          :disabled="disabled"
          @click="edit(index, record)"
          >修改
        </a-button>
        <a-button
          v-if="!record.edit"
          type="link"
          size="small"
          :disabled="disabled"
          >删除</a-button
        >
        <a-button
          v-if="record.edit"
          type="link"
          size="small"
          @click="editSave(index, record)"
          :disabled="disabled"
          >保存
        </a-button>
        <a-button
          v-if="record.edit"
          type="link"
          size="small"
          @click="cancelEdit(index, record)"
          :disabled="disabled"
        >
          取消
        </a-button>
      </template>
    </a-table>
  </div>
</template>

<script>
const tableColumn = [
  {
    key: "index",
    title: "序号",
    dataIndex: "index",
    width: 55,
    align: "center",
    scopedSlots: { customRender: "index" },
  },
  {
    key: "columnType",
    title: "数据库类型",
    dataIndex: "columnType",
    width: 180,
    scopedSlots: { customRender: "columnType" },
  },
  {
    key: "javaType",
    title: "Java类型",
    dataIndex: "javaType",
    width: 180,
    scopedSlots: { customRender: "javaType" },
  },
  {
    key: "importPackage",
    title: "是否导包",
    dataIndex: "importPackage",
    width: 180,
    scopedSlots: { customRender: "importPackage" },
  },
  {
    key: "packageName",
    title: "包名",
    dataIndex: "packageName",
    width: 180,
    scopedSlots: { customRender: "packageName" },
  },
  {
    title: "操作",
    key: "Action",
    scopedSlots: { customRender: "Action" },
    fixed: "right",
    width: 280,
    align: "center",
  },
];
export default {
  name: "type-mapper-config",
  props: {
    disabled: {
      type: Boolean,
      default: true,
    },
    config: {
      type: Array,
      default() {
        return null;
      },
    },
  },
  data() {
    return {
      tableData: [],
      columns: tableColumn,
      editRow: null,
    };
  },
  created() {

  },
  methods: {
    render(data){
      let tableData = [...data];
      tableData.forEach((item) => {
        item.edit = false;
      });
      this.tableData = tableData;
    },
    edit(index, row) {
      this.editRow = { ...row };
      row.edit = true;
      this.$set(this.tableData, index, row);
    },
    editSave(index, row) {
      if (!this.editRow.columnType || !this.editRow.javaType) {
        this.$message.error("数据库类型或Java类型不能为空");
        return;
      }

      if (this.editRow.importPackage && !this.editRow.packageName) {
        this.$message.error("包名不能为空");
        return;
      }

      row.edit = false;
      this.$set(this.tableData, index, { ...this.editRow });
      this.editRow = null;
    },
    cancelEdit(index, row) {
      this.editRow = null;
      if (row.isAdd) {
        this.tableData.splice(0, 1);
      } else {
        row.edit = false;
        this.$set(this.tableData, index, row);
      }
    },
    importPackageChange(e) {
      this.editRow.importPackage = e;
    },
    addRow() {
      let tableData = [...this.tableData];
      tableData.forEach((item) => {
        item.edit = false;
      });
      const newRow = {
        columnType: null,
        javaType: null,
        importPackage: false,
        packageName: null,
        edit: true,
        isAdd: true,
      };
      this.editRow = { ...newRow };
      tableData.splice(0, 0, newRow);
      this.tableData = tableData;
    },
    getConfig() {
      let tableData = [...this.tableData];
      tableData.forEach((item) => {
        delete item.edit;
        delete item.isAdd;
      });
      return { name: "typeMapperConfig", config: tableData };
    },
  },
  watch: {
    config: {
      deep: true,
      handler(val) {
        this.render(val)
      },
    },
  },
};
</script>

<style scoped lang="less">
.view-header {
  padding-bottom: 20px;
}

/deep/ .ant-table {
  .ant-table-thead > tr > th {
    color: #909399;
    font-weight: 500;
  }

  .ant-table-thead > tr > th {
    background-color: #fff;
  }
}

/deep/ .ant-table-small {
  border: none;
}
</style>